@import '../../assets/css/var.scss';
@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.wallet {
  &-wrap-modal-title {
    padding: 16px 20px 12px;
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
  }
  &-wrap {
    display: flex;
    align-items: center;
    padding: 10px 30px 30px 20px;
  }

  &-wrap-l {
    flex: 1;
    margin-right: 10px;
  }

  &-wrap-info {
    display: flex;
    align-items: center;
  }

  &-wrap-mobile {
    display: flex;
    align-items: center;
    margin-bottom: 17px;
    &-address-wrap {
      display: flex;
      align-items: center;
    }
  }

  &-logo {
    width: 40px;
    height: 40px;
    margin-right: 12px;
  }

  &-info {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    color: $color-text-default-5;
  }

  &-btn {
    background: $color-button-primary;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 600;
    color: $white;
    line-height: 17px;
    padding: 6px 28px;
    cursor: pointer;
  }

  &-address {
    margin-top: 5px;
    font-size: 12px;
    font-weight: 400;
    color: $color-text-default;
    line-height: 17px;
  }

  &-mobile-address {
    font-size: 16px;
    font-weight: 400;
    color: $color-text-default;
    line-height: 17px;
  }

  &-label {
    font-size: 12px;
    font-weight: 400;
    color: $color-text-default;
    opacity: 0.5;
  }

  &-copy {
    display: inline-block;
    width: 12px;
    height: 14px;
    cursor: pointer;
    @include darkThemeOnly {
      background: url('../../assets/images/menus/file_copy.svg') no-repeat center / contain;
    }
    @include lightThemeOnly {
      background: url('../../assets/images/icon_file_copy_light.png') no-repeat center / contain;
    }
    vertical-align: middle;
    margin-left: 8px;
  }
}

.wallet-address-modal.ant-modal-wrap {
  .ant-modal-content {
    padding: 0;
    background-color: #362d4a;
    .dark-table-container {
      border-radius: 0;
    }
    @include lightThemeOnly {
      background-color: #f5f5f5;
    }
  }
  .dark-table-container.ant-table-wrapper .ant-table-pagination {
    background-color: $color-large-range-card-bg;
  }
  .dark-table-container.ant-table-wrapper {
    .ant-table-tbody > tr.ant-table-row:hover > td {
      background-color: $color-text-default-05;
    }
  }
  .dark-table-loading + .ant-spin-text {
    color: $color-text-default-5 !important;
    text-shadow: none !important;
  }
  .ant-tabs-content {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    overflow: hidden;
  }
}

.dark-modal.wallet-modal.ant-modal {
  .ant-modal-header {
    padding-top: 10px;
    padding-bottom: 30px;
  }

  .ant-modal-body {
    padding: 0 20px 20px !important;
  }
  .wallet-item {
    display: flex;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;
    padding: 20px 30px;
    background: $color-text-default-06;
    border-radius: 10px;
    font-size: 17px;
    margin-bottom: 20px;
    border: 2px solid transparent;
    &:hover {
      background-color: $color-text-default-1;
      border-color: $color-text-default-1;
    }
    img {
      width: 40px;
      height: 40px;
    }
    .wallet-item-logo {
      flex: 1;
      text-align: left;
    }
    .wallet-item-name {
      // flex: 1;
      // text-align: right;
      // margin-left: 10px;
    }
    .wallet-item-usetip {
      padding: 12px;
      margin-left: 12px;
    }
  }
  .tronlink-tips {
    margin-top: 20px;
    .tronlink-tips-item {
      margin-bottom: 8px;
      text-align: center;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .tronlink-tips-text {
      font-size: 15px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 21px;
    }
    .tronlink-tips-link {
      margin-right: 10px;
      color: $color-a;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .address-con {
    display: flex;
    .address-logo {
      margin-right: 12px;
      width: 40px;
      height: 40px;
    }

    .tip-text {
      text-align: left;
      font-size: 14px;
      line-height: 16px;
      color: #7c8ead;
    }

    .address-tex {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
    }

    .copy {
      color: #5916e1;
    }
  }

  .ant-modal-close {
    right: 12px;
    top: 5px;
  }
}

.dark-modal.ant-modal-wrap,
.dark-modal.ant-modal,
.dark-modal.ant-modal-wrap.token-select-modal {
  .sun-stake-logo {
    vertical-align: middle;
  }
}

.dark-tabs {
  &-header {
    display: flex;
  }
  .ant-tabs-tabpane-active {
    background: $color-account-card-bg-active;
  }

  .dark-tabs-item,
  .dark-tabs-item-active {
    flex: 1;
    height: 38px;
    line-height: 38px;
    text-align: center;
    border: none;
    border-radius: 10px 10px 0 0;
    color: $color-text-default-5;
    font-size: 15px;
    font-weight: 400;
    cursor: pointer;
  }

  .dark-tabs-item {
    background-color: $color-account-card-bg-default;
  }
  .dark-tabs-item-active {
    background: $color-account-card-bg-active;
    color: $color-text-default;
    position: relative;
  }
  .ant-table-footer {
    background-color: $color-account-card-bg-active !important;
  }
}

.dark-table.ant-table-wrapper {
  .ant-table {
    background: transparent;
  }
  .ant-empty {
    opacity: 0;
    display: hidden;
  }

  .ant-table-tbody > tr > td {
    border-bottom: 1px $color-text-default-06;
  }

  .ant-table-tbody > tr.ant-table-row:hover > td {
    background: $color-text-default-06;
  }
  .ant-table-tbody > tr.ant-table-placeholder:hover > td {
    background: transparent;
  }
  .ant-table-tbody > tr.ant-table-placeholder {
    background: transparent;
    color: $color-text-default-06;
  }
  .ant-spin-blur::after {
    //background: rgba(255, 255, 255, 0.1);
    opacity: 0;
  }
  .ant-spin-nested-loading > div > .ant-spin {
    background: transparent;
  }
  .ant-spin-dot-item {
    background-color: $color-text-default;
  }
  &-loading {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
  }
  .ant-table-footer {
    background: transparent;
  }
  .ant-spin-nested-loading > div > .ant-spin .ant-spin-text {
    color: $color-text-default-5;
    font-size: 14px;
    text-shadow: none;
    font-weight: 400;
  }
}

.trade-status-tag {
  border-radius: 11px;
  font-size: 12px;
  font-weight: 600;
  line-height: 17px;
  padding: 2px 10px;
  display: inline-block;
  &-bar {
    border-radius: 0px 100px 100px 0px;
  }
}

.trade-cell-light {
  font-size: 15px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 21px;
}

.trade-cell-gray {
  font-size: 12px;
  font-weight: 400;
  color: $color-text-default-5;
  line-height: 17px;
}

.trade-pool-info {
  display: inline-block;
  vertical-align: top;
  margin: 0 10px;
}

.trade-text-right {
  text-align: right;
}

.trade-liquidity-token-wrap {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.trade-liquidity-token-from {
  // width: 40%;
  line-height: 21px;
  margin-top: 8px;
  margin-right: 8px;
  display: flex;
  align-items: center;
}
.trade-liquidity-token-to {
  display: flex;
  align-items: center;
}
.trade-list-vertical-bottom {
  vertical-align: bottom;
}
.trade-liquidity-token-name {
  margin: 0 12px 0 6px;
  font-size: 15px;
}

.mobile-account-center-modal.ant-drawer {
  .ant-drawer-content {
    background-color: $color-modal-bg;
  }
  .ant-drawer-body {
    padding: 20px 15px;
    @include lightThemeOnly {
      background: #f2f1f4;
    }
  }

  .wallet-wrap {
    padding: 0;
    margin-bottom: 22px;
    .wallet-address {
      font-size: 16px;
      line-height: 22px;
      font-weight: 400;
    }
  }

  .extension-from {
    padding: 0;
  }
}
.mobile-dark-tabs {
  &-header {
    display: flex;
  }

  &-item,
  &-item-active {
    font-size: 14px;
    font-weight: 400;
    color: $color-text-default-5;
    line-height: 20px;
    margin-right: 20px;
    padding-bottom: 20px;
    cursor: pointer;
  }
  &-item:last-child {
    margin-right: 0;
  }
  &-item-active {
    color: $color-text-default;
    position: relative;

    &:after {
      position: absolute;
      bottom: 14px;
      transform: translateX(-50%);
      left: 50%;
      content: '';
      width: 20px;
      height: 4px;
      background: linear-gradient(135deg, #9017ff 0%, #5a14e1 100%);
      border-radius: 2px;
    }
  }
}

.mobile-trade-item {
  background: $color-search-bg;
  border-radius: 5px;
  margin-bottom: 10px;
  user-select: none;
  &-header {
    height: 33px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $color-text-default-06;
    &-value {
      flex: 1;
      margin: 0 10px;
      font-size: 12px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 17px;
    }
    &-date {
      font-size: 12px;
      font-weight: 400;
      color: $color-text-default-5;
      line-height: 17px;
      margin-right: 10px;
    }
  }
  &-content {
    padding: 10px;
  }
  &-liquidity {
    .mobile-trade-item-content {
      padding: 10px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    .trade-token-item {
      width: calc((100% - 26px) / 2);
      flex-shrink: 0;
      margin-top: 20px;
      &:nth-child(1) {
        margin-top: 0;
      }
      &:nth-child(2) {
        margin-top: 0;
      }
    }
  }
  .sun-stake-logo {
    margin-right: 10px;
  }
}
.trade-font {
  &-14 {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }
  &-w-600 {
    font-weight: 600;
  }
  &-12 {
    font-size: 12px;
    line-height: 17px;
    font-weight: 400;
  }
  &-light {
    color: $color-text-default;
  }
  &-gray {
    color: $color-text-default-5;
  }
}
.trade-token-item {
  display: flex;
  width: 100%;
  .sun-icon-img {
    margin-right: 10px;
  }
  &-amount {
    margin-bottom: 3px;
    display: flex;
    justify-content: space-between;
  }
  &-right {
    flex: 1;
  }
}

@keyframes alwaysLoading {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.infinite-loading {
  color: $color-text-default-5;
  text-align: center;
  &-circle {
    animation: alwaysLoading 700ms ease infinite;
    overflow: hidden;
  }
}
.account-list-empty.sun-empty-wrap {
  margin-bottom: 0;
  background: transparent;
}
.account-list-tip {
  font-size: 12px;
  font-weight: 400;
  color: $color-text-default-3;
  line-height: 17px;
  text-align: center;
}
.trade-liquidity-pc-list-time {
  vertical-align: top;
  .trade-cell-gray {
    line-height: 1em;
    white-space: nowrap;
  }
}

// 消息中心样式
.account-notice-center {
  color: $color-text-default-7;
  .account-notice-list {
    max-height: 400px;
    overflow-y: auto;
    .account-notice-item {
      padding: 18px 20px;
      border-bottom: 1px solid $color-text-default-06;
      cursor: pointer;
      &:hover {
        background: $color-text-default-06;
      }

      &:last-child {
        border: 0;
      }
      .account-notice-title {
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        .account-notice-title-info {
          display: flex;
          flex: 1;
          align-items: center;
          .account-notice-icon {
            display: flex;
            opacity: 0.5;
            img {
              display: block;
            }
          }
          .account-notice-title-name {
            display: flex;
            align-items: center;
            margin-right: 10px;
            font-size: 16px;
            font-weight: 600;
            color: $color-text-default-4;
            line-height: 22px;
          }
          .account-notice-icon-new {
            display: block;
            width: 32px;
          }
          .account-notice-fee {
            margin-left: 6px;
            margin-right: 12px;
            font-size: 12px;
            color: $color-text-default-3;
          }
          .account-notice-version {
            padding: 0 6px;
            line-height: 16px;
            font-size: 12px;
            font-weight: 700;
            @include darkThemeOnly {
              background: rgba(193, 160, 255, 0.2);
              color: rgba(222, 205, 255, 0.5);
            }
            @include lightThemeOnly {
              background: #f3ecff;
              color: #722fff;
            }
            opacity: 0.5;
            border-radius: 2px;
          }
        }
        .account-notice-time {
          font-size: 12px;
          font-weight: 400;
          line-height: 17px;
          color: $color-text-default-3;
        }
      }

      .account-list-content {
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        color: $color-text-default-3;
      }
    }

    .account-notice-item-new {
      .account-notice-title {
        .account-notice-title-info {
          .account-notice-icon {
            opacity: 1;
          }
          .account-notice-title-name {
            color: $color-text-default;
          }
          .account-notice-fee {
            color: $color-text-default-5;
          }
          .account-notice-version {
            opacity: 1;
          }
        }
        .account-notice-time {
          color: $color-text-default-7;
        }
      }
      .account-list-content {
        color: $color-text-default;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
      }
    }
  }

  .account-list-empty {
  }
}

@include mobile-only {
  .dark-modal.wallet-modal.ant-modal {
    .ant-modal-header {
      padding-top: 0;
      padding-bottom: 14px;
    }

    .ant-modal-body {
      padding: 0 !important;
    }

    .wallet-item {
      padding: 10px 20px;
      font-size: 14px;
      img {
        width: 30px;
        height: 30px;
      }
    }

    .tronlink-tips {
      .tronlink-tips-item {
        font-size: 12px;
        margin-bottom: 6px;
        a {
          font-size: 12px;
        }
      }
      .tronlink-tips-text {
        font-size: 12px;
      }
    }

    .ant-modal-close-x {
      width: 30px;
      height: 30px;
      line-height: 30px;
      font-size: 22px;
    }
    .ant-modal-close .anticon-close {
      width: 22px;
      height: 22px;
    }
  }

  .account-notice-center {
    .account-notice-list {
      .account-notice-item {
        padding: 10px 0;
        .account-notice-title {
          .account-notice-title-info {
            .account-notice-title-name {
              margin-right: 5px;
              font-size: 14px;
              line-height: 20px;
            }
          }
          .account-notice-title-r {
            margin-left: 5px;
          }
          .account-notice-icon-new {
            width: 28px;
          }
        }

        .account-list-content {
          font-size: 13px;
          line-height: 20px;
        }
      }
    }
  }
}

.trade-list-table {
  box-shadow: none !important;
  .ant-table,
  .ant-pagination,
  .ant-table-footer {
    @include lightThemeOnly {
      background: #fff !important;
    }
  }
}
