@import '../../assets/css/var';
.header-toast {
  z-index: 10;
  background: #5915e1;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  text-align: center;
  position: relative;
  height: 40px;
  padding: 0 14px;

  .white-close {
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
  }

  .toast-text {
    display: inline-block;
    height: 40px;
    overflow: hidden;

    ul {
      display: block;
      height: 40px;
      line-height: 22px;
      margin: 0;
      position: relative;

      &.animate {
        -webkit-animation: 7.5s rowup linear infinite normal;
        animation: 7.5s rowup linear infinite normal;
      }
    }

    li {
      display: flex;
      align-items: center;
      height: 40px;
      text-align: center;
    }

    @keyframes rowup {
      0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      5% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      33% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      38% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      66% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      71% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }

      100% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }
    }

    a {
      color: #fff;
      line-height: 22px;
      padding: 9px 0;
      // height: 40px;
      display: block;
      text-align: center;
      box-sizing: border-box;
      text-decoration: none;
    }

    a .link {
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      color: #30084c;
      background: #ffffff;
      border-radius: 23px;
      padding: 2px 10px;
      margin-left: 15px;
      display: inline-block;
      height: 19px;
      vertical-align: top;
      margin-top: 2px;
    }
  }
}

