@include mobile-only {
  .header-toast .toast-text a .toast-view-more {
    display: none;
  }
  .header-toast .toast-text a {
    color: #fff;
    font-size: 14px;
    line-height: 18px;
    padding: 0;
    // height: 40px;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // display: -webkit-box;
    // -webkit-box-orient: vertical;
    // -webkit-line-clamp: 2; /* 限制显示的行数 */
    // overflow: hidden;
    // text-overflow: ellipsis;
  }
  .header-toast {
    .white-close {
      right: 10px;
    }

    .toast-text {
      display: inline-block;
      height: 40px;
      overflow: hidden;
      margin-left: -25px;

      ul {
        display: block;
        height: 40px;
        line-height: 20px;
        margin: 0;
        position: relative;
        padding: 0;

        &.animate {
          -webkit-animation: 7.5s rowup linear infinite normal;
          animation: 7.5s rowup linear infinite normal;
        }
      }

      @keyframes rowup {
        0% {
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }

        5% {
          -webkit-transform: translate3d(0, -40px, 0);
          transform: translate3d(0, -40px, 0);
        }

        33% {
          -webkit-transform: translate3d(0, -40px, 0);
          transform: translate3d(0, -40px, 0);
        }

        38% {
          -webkit-transform: translate3d(0, -80px, 0);
          transform: translate3d(0, -80px, 0);
        }

        66% {
          -webkit-transform: translate3d(0, -80px, 0);
          transform: translate3d(0, -80px, 0);
        }

        71% {
          -webkit-transform: translate3d(0, -120px, 0);
          transform: translate3d(0, -120px, 0);
        }

        100% {
          -webkit-transform: translate3d(0, -120px, 0);
          transform: translate3d(0, -120px, 0);
        }
      }

      a {
        color: #fff;
        font-size: 12px;
        line-height: 16px;
        padding: 0;
        // height: 40px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2; /* 限制显示的行数 */
        overflow: hidden;
        text-overflow: ellipsis;

        span {
          display: block;
          font-size: 12px;
          max-width: 290px;
        }
      }

      a .link {
        font-size: 12px;
        line-height: 15px;
        color: #30084c;
        background: #ffffff;
        border-radius: 23px;
        padding: 2px 10px;
        margin-left: 0;
        height: 17px;
        vertical-align: top;
        margin-top: 2px;
        display: none;
      }
    }
  }
}
