@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.m_lang_select {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $color-text-default;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  padding: 10px 20px;
  cursor: pointer;
  &_logo {
    width: 22px;
    transition: transform 0.3s ease;
    margin: 0 10px 0 0px;
  }
}

.m_pop_lang_select {
  color: $color-text-default;
  font-family: PingFang SC;
  font-size: 16px;
  font-weight: 600;
  line-height: 22.4px;
  text-align: right;

  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  .active_op {
    color: $color-a;
  }
  > div {
    margin-right: 44px;
    padding: 10px 0px;
    margin-top: 10px;
    text-align: right;
    &:hover {
      color: $color-a;
    }
  }
  img {
    width: 14px;
    height: 14px;
    transition: all 0.3s;
    margin-left: 8px;
  }
}
