@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.m-header-list-item {
  .m-header-main-item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin-bottom: 15px;

    .m-header-main-item-icon {
      width: 22px;
      height: 22px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .m-header-main-item-text {
      position: relative;
      margin: 0 10px;
      font-family: PingFangSC-Regular;
      font-weight: 600;
      font-size: 20px;
      color: $color-text-default;
      line-height: 28px;
      user-select: none;
      .m-header-item-icon {
        display: block;
        position: absolute;
        right: 4px;
        top: 0;
        transform: translate(100%, -50%);
      }
      .m-header-item-left-icon {
        margin-left: 8px;
      }
      .m-header-item-right-icon {
        margin-left: 8px;
      }
    }

    .m-header-main-item-icon2 {
      margin-left: auto;
      width: 14px;
      height: 14px;
      transition: all 0.3s;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  .m-header-sub-item {
    display: none;
    align-items: center;
    padding: 10px 20px 10px 52px;
    margin-bottom: 15px;
    // &:last-child {
    //   margin-bottom: 0;
    // }
    .m-header-sub-text {
      position: relative;
      margin: 0 10px;
      font-family: PingFangSC-Regular;
      font-weight: 600;
      font-size: 20px;
      color: $color-text-default;
      line-height: 28px;
      user-select: none;
    }
    .m-header-sub-icon {
      position: absolute;
      right: -4px;
      top: -8px;
      height: 20px;
      img {
        display: block;
        height: 100%;
      }
    }
    .m-header-sub-icon2 {
      margin-left: auto;
      width: 14px;
      height: 14px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .m-header-sub-tag {
      padding: 0 7px;
      line-height: 17px;
      border-radius: 17px;
      background: #decdff;
      white-space: nowrap;
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 12px;
      color: #8c51ff;
    }
  }
}
.m-header-list-item-active {
  .m-header-sub-item {
    display: flex;
  }
  .m-header-main-item-icon2 {
    transform: rotateZ(90deg);
  }
}
