@import '../../../../../assets/css/theme';

$close: #cccccc;

.v3-text-range {
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  cursor: pointer;
  > span {
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin-right: 4px;
  }
  &.close {
    color: $close;
    > span {
      background: $close;
    }
  }
  &.in {
    color: $color-safe-text;
    > span {
      background: $color-safe-text;
    }
  }
  &.out {
    color: $color-warning-text;
    > span {
      background: $color-warning-text;
    }
  }
}

.v3-range-badge {
  border-radius: 0px 10px 0px 10px;
  position: absolute;
  right: 0;
  top: 0;
  padding: 4px 15px;
  color: #ffffff;
  cursor: pointer;
  &.close {
    background: $close;
  }
  &.in {
    background: $color-safe-text;
  }
  &.out {
    background: $color-warning-text;
  }
}
