@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';
.new-mobile-nav-drawer {
  .ant-drawer-header {
    display: none !important;
  }
  .ant-drawer-body {
    padding: 0 !important;
  }
}
.new-mobile-header {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  height: 50px;
  background: $m-header-color-bg;
  .new-mobile-header-l {
    width: 76px;
    height: 30px;
    .new-mobile-logo {
      display: block;
      width: 100%;
      height: 100%;
      background: url(../../../assets/images/SUNLogo.svg) 0 0 no-repeat;
      background-size: 100%;
      @include lightThemeOnly {
        background-image: url(../../../assets/images/SUNLogo_light.svg);
      }
    }
  }
  .new-mobile-header-r {
    width: 18px;
    height: 16px;
    user-select: none;
    cursor: pointer;
    background: url(../../../assets/images/menus/ico_drawer.svg) 0 0 no-repeat;
    background-size: 100%;
    @include lightThemeOnly {
      background-image: url(../../../assets/images/menus/ico_drawer_light.svg);
    }
  }
}

.new-mobile-header-active {
  background: $m-header-color-active-bg;
}

.mobile-drawer-render {
  padding-top: 90px;
  // padding-bottom: 110px;
  width: 100%;
  height: 100%;
  background: $m-header-color-active-bg;
}
.mobile-drawer-render-footer-holder {
  width: 100%;
  height: 110px;
}
.mobile-drawer-render-nopadding {
  padding-top: 50px;
}

.new-mobile-header-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0px 20px 30px;
  .new-mobile-header-footer-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    background: #43385c;
    border-radius: 10px;
    border: 2px solid #c1a0ff;
    @include lightThemeOnly {
      background: #f5f0ff;
    }
  }

  .new-mobile-header-footer-icon {
    margin-right: 12px;
    width: 24px;
    height: 24px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  .new-mobile-header-footer-text {
    position: relative;
    color: #fff;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    @include lightThemeOnly {
      color: #5a14e1;
    }
  }
  .new-mobile-header-footer-notice-tag {
    position: absolute;
    right: 0;
    top: 0;
    padding: 0 6px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 12px;
    color: #ffffff;
    line-height: 17px;
    background-color: rgba(255, 71, 0, 1);
    border-radius: 17px;
    white-space: nowrap;
    transform: translate(100%, -50%);
  }

  .new-mobile-header-footer-connect {
    background: #8c51ff;
    border-color: #8c51ff;
    .new-mobile-header-footer-text {
      color: #fff;
      @include lightThemeOnly {
        color: #fff;
      }
    }
    @include lightThemeOnly {
      background: #5a14e1;
      border-color: #5a14e1;
    }
  }

  .new-mobile-header-footer-disabled {
    opacity: 0.3;
    cursor: no-drop;
  }
}

.mobile-drawer-subNav-wrap {
  background: #1d132e;
  @include lightThemeOnly {
    background: #f2f1f4;
  }
}

.mobile-drawer-subNav-hide {
  display: none;
}
