@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.menu-hover-dropdown {
  position: relative;
  cursor: pointer;
  font-size: 0;
  margin-right: 30px;

  .menu-hover-dropdown-global {
    width: 18px;
    margin-right: 6px;
  }
  .lang-link {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    font-size: 16px;
    font-weight: 400;
    line-height: 18.4px;
    color: $color-text-default;
    white-space: nowrap;
  }

  .down-icon {
    margin-left: 6px;
    height: 10px;
    width: 10px;
    background-image: url(../../assets/images/down-white.svg);
    background-repeat: no-repeat;
    transition: transform 0.2s;
    @include lightThemeOnly {
      background-image: url(../../assets/images/down-white-light.svg);
    }
  }

  &:hover .down-icon {
    transform: rotate(-180deg);
  }

  &:hover .header-select-box {
    display: block;
  }

  .header-select-box,
  .header-select-box-pool {
    display: none;
    font-size: 14px;
    position: absolute;
    left: -22px;
    border-radius: 5px;
    z-index: 3;
    overflow: hidden;

    .lang-wrap {
      width: 112px;
      padding: 8px 0;
      margin: 0;
      text-align: center;
      border-radius: 5px;
    }
    .lang {
      width: 112px;
      padding: 18px 24px;
      margin: 0;
      text-align: center;
      border-radius: 5px;
      ul {
        border-radius: 5px;
        overflow: hidden;
      }
    }

    .header-wrapper {
      background: #4d4366;
      border-radius: 5px;
      overflow: hidden;
      box-shadow: 0px 0px 6px 0px rgba(37, 31, 50, 0.3);
      @include lightThemeOnly {
        background: #fff;
        box-shadow: 0px 0px 6px 0px rgba(37, 31, 50, 0.1);
      }
    }

    .h15 {
      height: 8px;
    }

    li.selected,
    li:hover {
      background: rgba(103, 86, 146, 1);
      @include lightThemeOnly {
        background: #f5f5f5;
      }
      border-radius: 5px;
    }
    li.disabled {
      cursor: not-allowed;
    }

    li a {
      display: inline-block;
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22.4px;
      color: $color-text-default !important;
      @include lightThemeOnly {
        color: $color-text-default-7 !important;
      }
    }
  }

  .lang-wrap {
    width: 86px;
    padding: 8px 0;
    margin: 0;
    text-align: center;
    border-radius: 5px;
  }
  .lang {
    width: 86px;
    padding: 8px 0;
    margin: 0;
    text-align: center;
    ul {
      border-radius: 5px;
      overflow: hidden;
    }
  }
}
