@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.mobile-drawer-main {
  height: 100%;
  overflow-y: scroll;
}
.m-header-list {
  height: calc(100% - 80px);
  overflow: auto;
}

.m-header-mode {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  .m-header-mode-icon {
    width: 22px;
    height: 22px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .m-header-mode-text {
    margin: 0 10px;
    font-family: PingFangSC-Regular;
    font-weight: 600;
    font-size: 20px;
    color: $color-text-default;
    line-height: 28px;
    user-select: none;
  }
  .m-header-mode-icon2 {
    margin-left: auto;
    width: 44px;
    height: 26px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}
